import { Subject } from "@mui/icons-material";
import { observable, Observable } from "rxjs";
import { ajax, AjaxResponse } from "rxjs/ajax";

export class AwsApiClient {

  constructor() {}
 
  private static readonly URL_BASE: string = "https://ixnxniutwi.execute-api.ap-northeast-1.amazonaws.com";

  /**
   * ユーザー一覧の取得
   * @param deliOnly メール配信者だけを取得するか
   * @returns レスポンス
   */
  static getUsers(deliOnly: boolean) : Observable<AjaxResponse<any>> {
    return ajax.post(`${AwsApiClient.URL_BASE}/users`,
      {
        "deliOnly": true,
        "addAll": true
      }, 
      { 
        'Content-Type': 'application/json' 
      })
  }

  /**
   * メール送信
   * @param message メッセージ
   * @returns レスポンス
   */
  static sendMail(message: MailMessage): Observable<AjaxResponse<any>> {
    //console.log("start send");
    return ajax.post(`${AwsApiClient.URL_BASE}/mail/send`,
      message, 
      { 
        'Content-Type': 'application/json' 
      })
  }

}


export interface MailMessage {
  to: string[]
  subject: string;
  body: string;
}