import { ofType } from "redux-observable";
import { map, mergeMap } from "rxjs";
import { ajax } from "rxjs/ajax";
import { AwsApiClient } from "../../util/AwsApiClient";

// interface
export interface UserState {
  users: User[]
}
export interface User {
  id: string,
  IsMailDeli: string,
  MailAddress: string,
  MailDeli: boolean
  MailEnc: string,
  UserId: string,
  UserName: string,
  UserSeq: number,
}

// action creators
export const FETCH_USERS = "user/FETCH_USERS";
export const FETCH_USERS_FULFILLED = "user/FETCH_USERS_FULFILLED";

export const SEND_MAIL = "user/SEND_MAIL";
export const SEND_MAIL_FULFILLED = "user/SEND_MAIL_FULFILLED";

// initial state
const initialState: UserState = { users: [] };

// reducer
const reducer = (state = initialState, action: any) => {
  switch (action.type) {
    case FETCH_USERS_FULFILLED:
      //console.log(action.payload)
      return { users: action.payload }
    default:
      return state
  }
};

// epic
export const fetchUserEpic = (action$: any) => action$.pipe(
  ofType(FETCH_USERS),
  mergeMap(action =>
    AwsApiClient.getUsers(true)
      .pipe(
        map(rs => rs.response),
        map(response => ({ type: FETCH_USERS_FULFILLED, payload: response }))
      )
  )
);

export default reducer;